import Navbar from './Navbar'
import Footer from './Footer'

const Layout = (props) => (
    <div id="Layout">
        {/* <Navbar/>
        <br/><br/><br/> */}
          {props.children}
        <Footer/>
        <style jax>
        {`
        div#br {
            style=clear:both;
        }
        #Layout {
            padding:0px 16px 0px 16px;
        }
        `}
        </style>

    </div>
)

export default Layout


